.popup__close-button {
  position: absolute;
  width: 32px;
  height: 32px;
  background-color: transparent;
  background-image: url(../../../images/button-popup-close.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  margin: 0;
  padding: 0;
  top: -40px;
  right: -40px;
  cursor: pointer;
}

@media screen and (max-width: 610px) {
  .popup__close-button {
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0;
  }
}